import React,{useState,useEffect} from "react"
import axios from "axios"
import "./orderdetail.css"
function OrderPage() {
	// body...
	const [order,setOrder]=useState([])
	const [restList,setRestList]=useState([])
     const [id,setId]=useState('')
     const [time,setTime]=useState('')
	const [data,setData]=useState({})
	const [user,setUser]=useState({})
	 useEffect(()=>{
        axios.get('https://indapi.kumba.io/webdev/assignment')
         .then((result)=>{
         	setOrder(result.data.items)
         	if (result.data.items.length>5) {
         		hanldeLargeList()
         	}
         	setUser(result.data.user)
         	setTime(result.data.createdAt)
         	setId(result.data.order_id)
         	setData(result.data.restaurant)})
   },[])
	 let sum=0;
                    order.forEach((item)=>{
                        sum=sum+item.price+(item.price*item.tax_pct)/100;
                    })
    let item_total=0;
    order.forEach((item)=>{
      item_total=item_total+item.price;
    })
    let total_tax=0;
      order.forEach((item)=>{
        total_tax=total_tax+(item.price*item.tax_pct)/100;
      })
  const hanldeLargeList=()=>{
       if (order.length>5) {
       	setRestList(order.slice(5,));
       	setOrder(null);
       }
  }
  let flag=false;
  let moreItem;
  const showMore=()=>{
     flag=true;
     moreItem=restList.map((item)=>
       <li key={item.name}>{item.name}<br/><small>
                Category:{item.category} , Price:{item.price}INR </small></li>
                     )
  }
	 return(
	 	<div id="summary" className="order-page">
	 	<a href="#" onClick={()=>window.print()} style={{marginTop:"20px",paddingLeft:"20px",padding:"10px",margin:"10px"}} >Download Order Reciept <i className="material-icons">get_app</i></a>
              <header className="order-header">
              <h1 className="title">Order Summary</h1>
              </header>
              <div className="metadata">
              <h3>Order Id:{id}</h3>
              <h3>Order Placed at:{time}</h3>
              </div>
              <div className="hotel">
              <h3>Ordered From:</h3>
              <h4 >{data.name}</h4>
              <h4>Location : {data.street}</h4>
              <h4>{data.city},{data.state}<br/>Pin code : {data.zipcode}</h4>
              </div>
              <div className="reciept-info">
              <h3>Recipient Info:</h3>
              <h4>Delivery To : {user.name}</h4>
              <h4>Address to be deliverd at : {user.address}</h4>
              <h4>Phone Number : {user.phone}</h4>
              </div>
                <div className="item-info">
                <h3>Ordered Item's:<ol>{order.map((item)=><li key={item.name} className="items">{item.name}<br/><small>
                Category:{item.category} ,<br/> Price:{item.price}INR, Tax:{item.tax_pct}% </small></li>)}</ol></h3>
                {
                	order.length && flag>5?<a href="#seemoreitems" onClick={showMore}>see more</a>:null
                }
                {
                	flag?<ol>{moreItem}</ol>:null
                }
                </div>
                <div className="total">

                {
                   item_total>0?<h3>Total item price = {item_total}INR</h3>:null
                }
                {
                   total_tax>0?<h3>Total tax = {total_tax}INR</h3>:null
                }
                {
                   sum>0?<h3>Total Billing Amount = {sum}INR</h3>:null

                }
                <h3 className="footer-text"> Happy Eating!</h3>
                <b style={{color:"red",fontSize:"25px"}}>(*This reciept is generated for project purpose only,not valid any where.)</b>
                </div>
	 	</div>
	 	)
}
export default OrderPage;