import React,{useState,useEffect} from 'react';
import axios from 'axios';
import Dashboard from "./dashboard";
import {BrowserRouter as Router,Link} from "react-router-dom";
import {Container,Card,Button,Image,Modal,Form} from "react-bootstrap";
import "./user.css";
import EditFoodPrefrence from './editfood';
function User() {
  const [user,setUser]=useState({})
  const [flag,setFlag]=useState(false)
  const [name,setName]=useState('')
  const [address,setAddress]=useState('')
  const [phone,setPhone]=useState('')
   useEffect(()=>{
        axios.get('https://indapi.kumba.io/webdev/assignment')
        .then((result)=>{setUser(result.data.user)
          console.log(result)})
   },[])
  const handleName=(event)=>{
    setName(event.target.value)
  }
  const handleAddress=(event)=>{
    setAddress(event.target.value)
  }
  const handlePhone=(event)=>{
    setPhone(event.target.value)
  }
  const handleSubmit=(event)=>{
    event.preventDefault();
    const update={
      "name":name,
      "address":address,
      "phone":phone
    }
    axios.post('https://indapi.kumba.io/webdev/assignment',update)
    .then(alert("Update Successful"))
  }
  return (
    <div>
    <Dashboard/>  
    <Container className="user">
      <Card>
       <Card.Title className="profile">
        <Image src="male.png"/>
         <h3>"{user.about}"</h3>
       </Card.Title>
       <Card.Body className="card-body">
        <h3>- {user.name}</h3>
       <h4>Costumer ID:{user.id}</h4>
       <h4>Phone Number:{user.phone}</h4>
       </Card.Body>
       <Card.Footer>
       <h4>Address:{user.address}</h4>
       </Card.Footer>
      </Card>
      <h4>Request Edit <Link to="#editpersonalinfo" onClick={()=>setFlag(true)}><i className="material-icons">create</i></Link></h4>
          </Container>
        <Modal
    show={flag}
    animation="true"
    onHide={()=>setFlag(false)}
    style={{backgroundColor:"#99ffff"}}
    >
    <Modal.Body>
    <Form onSubmit={handleSubmit} className="update-info">
      <label>Name:</label>
      <Form.Control type="text" defaultValue={user.name} onChange={handleName}/>
      <label>Address:</label>
      <Form.Control type="text" defaultValue={user.address} onChange={handleAddress} />
      <label>Contact:</label>
      <Form.Control type="text" defaultValue={user.phone} onChange={handlePhone} />
      <Button type="submit">Change</Button>
    </Form>
    </Modal.Body>
    <Modal.Footer>
    <h4 style={{color:"magenta"}}>*Click any where outside box to  exit</h4>
    </Modal.Footer>
    </Modal>
    <EditFoodPrefrence/>
       </div>


  );
}

export default User;