import React,{useState,useEffect} from "react";
import {OverlayTrigger,Tooltip} from "react-bootstrap";
import {BrowserRouter as Router,Link} from "react-router-dom"
import "./dashboard.css";
import axios from 'axios'
function Dashboard(argument) {
	// body...
   const [user,setUser]=useState({})
   const [like,setLike]=useState([])
   const [disLike,setDisLike]=useState([])
   useEffect(()=>{
        axios.get('https://indapi.kumba.io/webdev/assignment')
         .then((result)=>{setUser(result.data.user)})
   },[])
  const handleDashboard=()=>{
  document.getElementById('dashboard').classList.toggle("block");
  console.log(user.likes)
  setLike(user.likes.map((like)=><li className="sublist">{like}</li>))
  	setDisLike(user.dislikes.map((result)=><li className="sublist">{result}</li>))
  }
	return(
		<div className="dashboard"> 
				<h3><button id="btn" className="dashboard-button" onClick={handleDashboard}><i className="material-icons">menu</i></button></h3>
		 <div id="dashboard" className="dashboard-list">
		 <ul className="list">
		 <li className="dashboard-title">DASHBORD</li>
		 <OverlayTrigger
      key='1'
      placement='right'
      overlay={
        <Tooltip id={`tooltip-right`}>
        <ul>
          {like}
        </ul>
        </Tooltip>
      }
    ><li className="list-item">Liked Food</li>
    </OverlayTrigger>
     <OverlayTrigger
      key='2'
      placement='right'
      overlay={
        <Tooltip id={`tooltip-right`}>
        <ul>
          {disLike}
        </ul>
        </Tooltip>
      }
    >
		 <li className="list-item">Disliked Food</li>
		 </OverlayTrigger>
		 <li className="list-item"><Link to="/ordersummaryinfo" style={{color:"black"}}>See order's</Link></li>
		 <li className="list-item"><a href="#editfoodlist" style={{color:"black"}}>Edit Food Choices</a></li>
		 </ul>
		 </div>
       </div>
		)
}
export default Dashboard;