import React,{useState,useEffect} from 'react';
import User from "./components/user-info";
import {BrowserRouter as Router ,Link,Switch,Route} from "react-router-dom";
import OrderPage from "./components/orderdetail";
function App() {
    return (
    <div className="App">
              <Router>
              <Switch>
              <Route exact path={process.env.PUBLIC_URL+"/"} component={User}/>
              <Route exact path="/ordersummaryinfo" component={OrderPage}/>
              </Switch>
              </Router>
           </div>
  );
}

export default App;
