import React,{useState,useEffect} from "react";
import {Image} from 'react-bootstrap';
import axios from 'axios';
import ChipInput from "material-ui-chip-input";
import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
import "./editfood.css";


function EditFoodPrefernce(argument) {
	// body...
	const [like,setLike]=useState([])
   const [disLike,setDisLike]=useState([])

	useEffect(()=>{
          axios.get('https://indapi.kumba.io/webdev/assignment')
          .then((result)=>{
                    setLike(result.data.user.likes)
                    setDisLike(result.data.user.dislikes)
          })
	},[])
    
    const handleLikeAdd=(event)=>{
    	like.push(event.target.value)
      axios.post('https://indapi.kumba.io/webdev/assignment',{likes:like})
    }
    const handleDelete=(event)=>{
      like.forEach((item)=>{
        if (item==event.target.value) {
           item=null
           axios.post('https://indapi.kumba.io/webdev/assignment',{likes:like})
        }
      });
    }
    const handleDisLikeAdd=(event)=>{
      disLike.push(event.target.value)
      axios.post('https://indapi.kumba.io/webdev/assignment',{dislikes:disLike})
    }
    const handleDisLikeDelete=(event)=>{
      disLike.forEach((item)=>{
        if(item==event.target.value){
          item=null
          axios.post('https://indapi.kumba.io/webdev/assignment',{dislikes:disLike})
        }
      });

    }
	return(<div className="editprefernce" id="editfoodlist">
           <Image src="food.jpeg" className="foodgif"/>
		    <h3 style={{padding:"10px"}}>Update your food prefernce here and  Experince more delighted food</h3>
		      <h4 style={{padding:"10px",marginBottom:"10px"}}>Most Ordered Taste:</h4>
              <ChipInput
                  label="like's"
                  defaultValue={like}
              		onAdd={handleLikeAdd}
                  onDelete={handleDelete}   
               />
          <h4 style={{padding:"10px"}}>Least Ordered Taste:</h4>
           <ChipInput
                  label="dislike's"
                  defaultValue={disLike}
                  onAdd={handleDisLikeAdd}
                  onDelete={handleDisLikeDelete}   
                  fullWidth
               />

              		              
		</div>)
}
export default EditFoodPrefernce;